g-box .trumbowyg-editor {\n    margin: 0 auto;\n}\n.trumbowyg-box.trumbowyg-fullscreen {\n    background: #FEFEFE;\n}\n.trumbowyg-editor, .trumbowyg-textarea {\n    position: relative;\n    -webkit-box-sizing: border-box;\n    -moz-box-sizing: border-box;\n    box-sizing: border-box;\n    padding: 1% 2%;\n    min-height: 300px;\n    width: 100%;\n    border-style: none;\n    resize: none;\n    outline: none;\n}\n.trumbowyg-box-blur .trumbowyg-editor * {\n    color: transparent !important;\n    text-shadow: 0 0 7px #333;\n}\n.trumbowyg-box-blur .trumbowyg-editor img {\n    opacity: 0.2;\n}\n.trumbowyg-textarea {\n    position: relative;\n    display: block;\n    overflow: auto;\n    border: none;\n    white-space: normal;\n}\n.trumbowyg-editor[contenteditable=true]:empty:before {\n    content: attr(placeholder);\n    color: #999;\n}\n.trumbowyg-button-pane {\n    position: relative;\n    width: 100%;\n    background: #ecf0f1;\n    border-bottom: 1px solid #d7e0e2;\n    margin: 0;\n    padding: 0;\n    list-style-type: none;\n    line-height: 10px;\n    -webkit-backface-visibility: hidden;\n    backface-visibility: hidden;\n}\n.trumbowyg-button-pane li {\n    display: inline-block;\n    text-align: center;\n    overflow: hidden;\n    width: 35px;\n    height: 35px;\n}\n.trumbowyg-button-pane li.trumbowyg-separator {\n    width: 1px;\n    background: #d7e0e2;\n    margin: 0 5px;\n    height: 35px;\n}\n.trumbowyg-button-pane.trumbowyg-disable li:not(.trumbowyg-not-disable) button:not(.trumbowyg-active) {\n    opacity: 0.2;\n    cursor: default;\n}\n.trumbowyg-button-pane.trumbowyg-disable li.trumbowyg-separator {\n    background: #e3e9eb;\n}\n.trumbowyg-button-pane:not(.trumbowyg-disable) li button:hover, .trumbowyg-button-pane:not(.trumbowyg-disable) li button:focus, .trumbowyg-button-pane li button.trumbowyg-active, .trumbowyg-button-pane li.trumbowyg-not-disable button:hover, .trumbowyg-button-pane li.trumbowyg-not-disable button:focus {\n   /*  background-color: #FFF; */\n    outline: none;\n}\n.trumbowyg-button-pane li .trumbowyg-open-dropdown:after {\n    display: block;\n    content: \" \";\n    position: absolute;\n    top: 25px;\n    right: 3px;\n    height: 0;\n    width: 0;\n    border: 3px solid transparent;\n    border-top-color: #555;\n}\n.trumbowyg-button-pane .trumbowyg-buttons-right {\n    float: right;\n    width: auto;\n}\n.trumbowyg-button-pane .trumbowyg-buttons-right button {\n    float: left;\n}\n.trumbowyg-dropdown {\n    width: 200px;\n    border: 1px solid #ecf0f1;\n    padding: 5px 0;\n    border-top: none;\n    background: #FFF;\n    margin-left: -1px;\n    -webkit-box-shadow: rgba(0, 0, 0, 0.1) 0 2px 3px;\n    box-shadow: rgba(0, 0, 0, 0.1) 0 2px 3px;\n}\n.trumbowyg-dropdown button {\n    display: block;\n    width: 100%;\n    height: 35px;\n    line-height: 35px;\n    text-decoration: none;\n    background: #FFF;\n    padding: 0 14px;\n    color: #333;\n    border: none;\n    cursor: pointer;\n    text-align: left;\n    font-size: 15px;\n    -webkit-transition: all 0.15s;\n    -o-transition: all 0.15s;\n    transition: all 0.15s;\n}\n.trumbowyg-dropdown button:hover, .trumbowyg-dropdown button:focus {\n    background: #ecf0f1;\n}\n/* Modal box */\n\n.trumbowyg-modal {\n    position: absolute;\n    top: 0;\n    left: 50%;\n    margin-left: -260px;\n    width: 520px;\n    height: 290px;\n    overflow: hidden;\n}\n.trumbowyg-modal-box {\n    position: absolute;\n    top: 0;\n    left: 50%;\n    margin-left: -250px;\n    width: 500px;\n    height: 275px;\n    z-index: 1;\n    background-color: #FFF;\n    text-align: center;\n    -webkit-box-shadow: rgba(0, 0, 0, 0.2) 0 2px 3px;\n    box-shadow: rgba(0, 0, 0, 0.2) 0 2px 3px;\n    -webkit-backface-visibility: hidden;\n    backface-visibility: hidden;\n}\n.trumbowyg-modal-box .trumbowyg-modal-title {\n    font-size: 24px;\n    font-weight: bold;\n    margin: 0 0 20px;\n    padding: 15px 0 13px;\n    display: block;\n    border-bottom: 1px solid #EEE;\n    co